.wrapper {
  display: flex;
  /* justify-content: space-between; */
}

.label {
  text-transform: uppercase;
  font-size: 20px;
  margin-right: 10px;
  color: var(--green);
  transition: all 200ms;
}

.label__focus {
  color: var(--grey);
  padding-top: 24px;
  margin-bottom: -10px;
}

.input {
  font-size: 2rem;
  max-width: 80px;
  text-align: center;
  padding-top: 8px;
  background-color: var(--green);
  color: var(--dark-green);
}

.input:focus {
  background-color: var(--grey);
}
