@import-normalize;
@import "@fontsource/aldrich";

:root {
  --grey: #dddfd4;
  --dark-green: #173e43;
  --green: #3fb0ac;
  --yellow: #fae596;
}

html {
  box-sizing: border-box;
  font-size: 16px;
  font-family: "Aldrich", sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
a {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
}

input,
button {
  font-family: "Aldrich", sans-serif;
  border: none;
}

input[type="number"] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

textarea:focus,
input:focus {
  outline: none;
}
