.setup {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.controls {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 10px;
}

.sequence {
  margin-top: 20px;
}

.heading-block {
  display: flex;
  justify-content: center;
  color: var(--yellow);
}

.heading {
  font-size: 20px;
  text-transform: uppercase;
}

.quantity {
  font-size: 10px;
  margin-left: 5px;
}

.sequence-order-wrapper {
  margin: 0 auto;
}

.sequence-order {
  display: flex;
  flex-wrap: wrap;
  width: 335px;
  justify-content: flex-start;
  gap: 7px;
  margin-top: 10px;
}

.sequence-number {
  min-width: 50px;
  min-height: 24px;
  font-size: 20px;
  padding-top: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--yellow);
  color: var(--dark-green);
}

.start {
  margin-top: 20px;
  padding: 17px 30px 10px;
  font-size: 32px;
  text-transform: uppercase;
  background-color: var(--yellow);
  color: var(--dark-green);
}
