.radio-set {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.radio-set-heading {
  font-size: 20px;
  color: var(--green);
  text-transform: uppercase;
}

.radio-group {
  display: flex;
  gap: 10px;
  margin-top: 5px;
}

.radio-input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.radio-input:checked + .radio-label {
  background-color: var(--grey);
}

.radio-label {
  width: 40px;
  height: 40px;
  display: block;
  text-align: center;
  padding-top: 3px;
  background-color: var(--green);
  color: var(--dark-green);
  font-size: 32px;

  transition: background-color 200ms;
}
